body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2 {
  color: black;
  font-size: 1.5em;
}

.App {
  display: grid;
  min-height: 100vh;
  grid-template-columns: 25% 1fr;
  grid-template-rows: 90px 1fr;
  grid-template-areas:
    "aside header"
    "aside main";
}

html,
body {
  height: 100%;
}

header {
  grid-area: header;
  background-color: #282c34;
  text-align: right;
  padding-right: 2em;
  padding-bottom: 1em;
  color: white;
}

main {
  grid-area: main;
}

button {
  border: none;
  height: auto;
  width: auto;
  padding: 10px;
  background: #8552f2;
  color: #ffffff;
  font-size: 0.8em;
}

h1 {
  font-size: 2em;
  padding-top: 15px;
  padding-bottom: 15px;
  background: -webkit-linear-gradient(left, #c594f7, #13cbf9);
  background: linear-gradient(90deg, #c594f7, #13cbf9);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.room-list {
  background-color: #282c34;
  min-height: 100vh;
  font-size: 1.5em;
  color: white;
  line-height: 1.6em;
  text-align: left;
  padding-left: 2em;
  grid-area: aside;
  line-height: 1em;
}

.Room-name {
  padding-bottom: 0px;
  padding-top: 25px;
}

.Delete-text:hover,
.rename-text:hover {
  color: #13cbf9;
}

.rename-room-modal,
.Delete-text,
.inline-block {
  display: inline-block;
  padding: 0px;
  font-size: 0.65em;
  color: gray;
}

Modal {
  display: grid;
  height: 100%;
  width: 100%;
}

.modal-form {
  margin: auto;
  padding: 30px;
}

.modal-input {
  width: 90%;
  height: 20px;
  padding: 5px;
  margin-bottom: 10px;
}

.Message-List {
  display: grid;
  grid-template-rows: auto 70px;
  height: 100%;
  grid-column: 1fr;
  grid-template-areas:
    "content"
    "footer";
}
.MessagesInRoom {
  background-color: white;
  text-align: left;
  margin-left: 1em;
  align-self: start;
  grid-area: content;
  width: auto;
}

ul {
  list-style-type: none;
  line-height: 150%;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-right: 10px;
  padding-right: 10px;
}

ul:nth-child(even) {
  background: white;
}
ul:nth-child(odd) {
  background: #e2d8f7;
}

.CreateNewMessage {
  grid-area: footer;
  background-color: white;
  padding: 10px;
}

.CreateNewMessage input {
  width: 85%;
  height: 25px;
  padding: 5px;
  margin-right: 10px;
}

.Message-Button {
  display: inline-block;
  border: none;
  height: 40px;
  padding: 10px;
  width: 10%;
  text-decoration: none;
  background: #8552f2;
  color: #ffffff;
  font-family: sans-serif;
  font-size: 1rem;
  text-align: center;
}

.message-date {
  position: absolute;
  right: 20px;
  font-size: 0.7em;
}

